.App {
  text-align: center;
  background-color: #282c34;
  height: 100vh;
  color: white;
  font-family: 'New Rocker';
}

.switch-wrapper {
  position: relative;
}

.switch-wrapper > div {
  position: absolute;
  /* height: 100vh; */
  width: 100%;
  /* position: fixed; */
  /* display: contents; */
}

.page{
  height: 100vh;
  width: 100%;
  position: fixed;
  display: contents;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.fade-exit{
  opacity: 1;
}
.fade-exit-active{
  opacity: 1;
  transition: "opacity 500ms ease-out";
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: 'New Rocker';
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
